import { FC } from 'react'
import { useRouter } from 'next/router'

import { LocalLink } from '@lidofinance/ui-primitives'

import { NavigationLinkStyle } from './styles'
import { INavigationLink } from './types'

export const NavigationLink: FC<INavigationLink> = ({ icon, title, href, external }) => {
  // TODO: use ...rest for styling
  // const { icon, title, href, external } = props

  const router = useRouter()

  const searchParamsString = new URLSearchParams(router.query as Record<string, string>).toString()
  // TODO: for `ref` and `embed` (Andrey) do we need it?
  const queryString = searchParamsString ? `?${searchParamsString}` : ''
  const pathWithQuery = href + `${queryString}`

  return external ? (
    <NavigationLinkStyle href={pathWithQuery} active={router.pathname === href} target="_blank">
      {icon}
      <span>{title}</span>
    </NavigationLinkStyle>
  ) : (
    // Fix hydration error https://github.com/vercel/next.js/issues/42358#issuecomment-1307230409
    <LocalLink href={href} passHref legacyBehavior>
      <NavigationLinkStyle active={router.pathname === href}>
        {icon}
        <span>{title}</span>
      </NavigationLinkStyle>
    </LocalLink>
  )
}
