import React, { FC, PropsWithChildren } from 'react'
import {
  CookiesTooltip,
  CookieThemeProvider,
  ToastContainer,
  migrationThemeCookiesToCrossDomainCookiesClientSide,
} from '@lidofinance/lido-ui'
import { ProviderWalletModal } from '@lidofinance/eth-ui-wallet-modal'
